import { Component } from '@angular/core';
import { AlertController } from 'ionic-angular';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { ContactProvider } from './../../providers/contact/contact';
import { Observable } from 'rxjs/Observable';
import { AngularFireDatabase } from 'angularfire2/database';
import 'rxjs/add/operator/map';

@IonicPage()
@Component({
  selector: 'page-mantimentos',
  templateUrl: 'mantimentos.html',
})
export class MantimentosPage {
  picToView2:string="assets/imgs/pouca.png";
  Goods: Observable<any>;
  Drugs: Observable<any>;
  good: any;
  drug: any;
  counterM: number;

  /*constructor(public navCtrl: NavController, public navParams: NavParams, private provider: ContactProvider, private db: AngularFireDatabase) {
    this.Goods = this.provider.getAll();
  }*/
  constructor(public navCtrl: NavController, public navParams: NavParams, private provider: ContactProvider, private db: AngularFireDatabase, public alertCtrl: AlertController,
  private toast: ToastController) {
    this.Goods = this.provider.getAllM();
    this.Drugs = this.provider.getAllD();
    this.counterM = 0;
  }



  setupManti(objeto){
    if(this.counterM == 0){
      if(objeto >= 6){
        this.picToView2="assets/imgs/muita.png";
      }else if( objeto <= 5 && objeto >=3 ){
        this.picToView2="assets/imgs/medio.png";
      }else if(objeto <= 2){
        this.picToView2="assets/imgs/pouca.png";
      }
    }
  }

  showPrompt() {
    const prompt = this.alertCtrl.create({
      title: 'Ração',
      message: "Os gatinhos foram alimentados, quantos kgs temos sobrando?",
      inputs: [
        {
          name: 'quantidade',
          placeholder: 'Quantidade'
        },
      ],
      buttons: [
        {
          text: 'Cancelar',
          handler: data => {

          }
        },
        {
          text: 'Salvar',
          handler: data => {

            return new Promise((resolve, reject) => {
              if ("a1b2c3") {
                this.db.list("mantimen/")
                  .update("a1b2c3", { racao: data.quantidade })
                  .then(() => resolve())
                  .catch((e) => reject(e));
                this.setupManti(data.quantidade);

              }
            })

          }
        }
      ]
    });
    prompt.present();
  }

  onScroll($event){
    if ((window.innerHeight + window.pageYOffset) >= document.body.offsetHeight) {
        document.getElementById('btadd').style.visibility = "visible";
    }else{
      document.getElementById('btadd').style.visibility = "hidden";
    }
  }


  newContactDrugs() {
    this.navCtrl.push('RemediosPage');

  }

  editContactDrugs(contact: any) {
    this.navCtrl.push('RemediosPage', { contact: contact });

  }

  removeContactD(key: string) {
    console.log(key);
    if (key) {
      this.provider.removeD(key)
        .then(() => {
          this.toast.create({ message: 'Contato removido sucesso.', duration: 3000 }).present();
        })
        .catch(() => {
          this.toast.create({ message: 'Erro ao remover o contato.', duration: 3000 }).present();
        });
    }
  }

}



